import React from 'react';
import { Row, Col } from 'antd';
import './style.scss';

export const StatisticBar = () => (
  <Row gutter={24} className="statistic-bar" type='flex' align="stretch">
    <Col span={24} className="statistic-bar-content">
      <Col xs={24} sm={24} md={8} lg={8} xl={7}>
        <Row>
          <Col span={18} className="title">S tin phi thu k:</Col>
          <Col span={6} className="result">1,000,000</Col>
          <Col span={18} className="title">S tin  thu k:</Col>
          <Col span={6} className="result">1,000,000</Col>
        </Row>
      </Col>
      <Col xs={24} sm={24} md={8} lg={8} xl={7}>
        <Row>
          <Col span={18} className="title">S tin thu tha k:</Col>
          <Col span={6} className="result">1,000,000</Col>
          <Col span={18} className="title">S tin cn phi thu k:</Col>
          <Col span={6} className="result">1,000,000</Col>
        </Row>
      </Col>
      <Col xs={24} sm={24} md={8} lg={8} xl={7}>
        <Row>
          <Col span={18} className="title">S tin thu tha cui k:</Col>
          <Col span={6} className="result">1,000,000</Col>
          <Col span={18} className="title">S tin cn phi thu cui cng:</Col>
          <Col span={6} className="result">1,000,000</Col>
        </Row>
      </Col>
      <Col xs={24} sm={24} md={4} lg={4} xl={3}>
        <Row style={{ height: '100%' }}>
          <Col span={18} className="title">Tng s SV:</Col>
          <Col span={6} className="result">20</Col>
        </Row>
      </Col>
    </Col>
  </Row>
);



