import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnExport from 'components/common/button/BtnExport';
import BtnView from 'components/common/button/BtnView';
import { Card, Row, Col, Button, Checkbox } from 'antd';
import { CartHeadAction, TableData } from 'components/common';
import { numberToMoney } from 'utils/formatUtil';
import { getStudents } from 'services/tuititionDeptStatistic';
import FormSearchStudent from './FormSearchStudent';
import { StatisticBar } from './StatisticBar';
import './style.scss';

class TuitionDeptChargeStatisticPage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {},
    addingStudents: {},
    addingStudentsTableData: {},
    deletingStudents: {},
  };

  onSelectForAddStudent = (selectedStudent, isSelect) => {
    const addingStudents = {...this.state.addingStudents};
    if(isSelect) {
      addingStudents[selectedStudent.studentId] = selectedStudent;
    } else {
      delete addingStudents[selectedStudent.studentId];
    }
    this.setState({ addingStudents });
  }

  onSelectAllForAddStudent = (selectedStudents, isSelect) => {
    if(isSelect) {
      const addingStudents = {};
      selectedStudents.forEach(student => {
        addingStudents[student.studentId] = student;
      });
      this.setState({ addingStudents });
    } else {
      this.setState({ addingStudents: {} });
    }
  }

  onSelectForRemoveStudent = (selectedStudent, isSelect) => {
    const deletingStudents = {...this.state.deletingStudents};
    console.log(selectedStudent);
    if(isSelect) {
      deletingStudents[selectedStudent.studentId] = selectedStudent;
    } else {
      delete deletingStudents[selectedStudent.studentId];
    }
    this.setState({
      deletingStudents,
    });
  }

  onSelectAllForRemoveStudent = (selectedStudents, isSelect) => {
    if(isSelect) {
      const deletingStudents = {};
      selectedStudents.forEach(student => {
        deletingStudents[student.studentId] = student;
      });
      this.setState({ deletingStudents });
    } else {
      this.setState({ deletingStudents: {} });
    }
  }

  onAddStudents = () => {
    const addingStudentsTableData = {...this.state.addingStudentsTableData};

    Object.entries(this.state.addingStudents).forEach(([key, value]) => {
      addingStudentsTableData[key] = value;
    });
    this.setState({ addingStudentsTableData });
  }

  onViewRegistedCredits = () => {
    console.log("View Registed Credits");
  }

  onSelectChargeType = () => {
    console.log("On Select Charge Type");
  }
  
  dataFormSeach = data => {
    this.setState({
      paramSearchClass: data,
    });
  };

  render() {
    const { reload, paramSearchClass, addingStudents } = this.state;
    const isBtnViewVisible = addingStudents ? Object.entries(addingStudents).length !== 1 : false;
    const isBtnExportAndPrintDisable = addingStudents ? Object.entries(addingStudents).length < 1 : false;
    console.log(paramSearchClass);
    const columns = [
      {
        title: 'M SV',
        dataIndex: 'studentId',
        key: 'studentId',
        align: 'center'
      },
      {
        title: 'H v tn',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Tn lp',
        dataIndex: 'class',
        key: 'class',
        align: 'center'
      },
      {
        title: 'N k trc',
        dataIndex: 'previousDebitSemester',
        key: 'previousDebitSemester',
        align: 'center',
        render: value => numberToMoney(value)
      },

      {
        title: 'Phi np k',
        dataIndex: 'requestMoneySemester',
        key: 'requestMoneySemester',
        align: 'center',
        render: value => numberToMoney(value)
      },
      {
        title: ' np k',
        dataIndex: 'submitMoneySemester',
        key: 'submitMoneySemester',
        align: 'center',
        render: value => numberToMoney(value)
      },
      {
        title: 'Hon tr k',
        dataIndex: 'returnMoneySemester',
        key: 'returnMoneySemester',
        align: 'center',
        render: value => numberToMoney(value)
      },

      {
        title: 'Thiu tha k',
        dataIndex: 'lackedMoneySemester',
        key: 'lackedMoneySemester',
        align: 'center',
        render: value => numberToMoney(value)
      },
      {
        title: 'Thiu tha cui cng',
        dataIndex: 'lackedRedundantMoney',
        key: 'lackedRedundantMoney',
        align: 'center',
        render: value => numberToMoney(value)
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        className: 'action-column',
        align: 'center',
        render: () => (
          <BtnView onClick={this.onViewRegistedCredits} />
        ),
      },
    ];

    const rowSelection = {
      onSelect: (record, selected) => this.onSelectForAddStudent(record, selected),
      onSelectAll: (selected, selectedRows) => this.onSelectAllForAddStudent(selectedRows, selected)
    };

    // const radioStyle = {
    //   display: 'block',
    //   height: '30px',
    //   lineHeight: '30px',
    // };

    const options = [
      { label: 'Bo him y t', value: 'Bo him y t' },
      { label: 'V sinh cng cng', value: 'V sinh cng cng' },
      { label: 'K tc x lin thng', value: 'K tc x lin thng' },
    ];

    return (
      <Card title="DS lp hc">
        <CartHeadAction>
          <div className="btn-group">
            <BtnExport
              className="btnExport"
              title="Xut Excel"
              disabled={isBtnExportAndPrintDisable}
            />
            <Button
              className="btnAction btnPrint"
              icon="printer"
              size="large"
              disabled={isBtnExportAndPrintDisable}
            >
              In
            </Button>
          </div>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchStudent dataForm={this.dataFormSeach} isBtnViewVisible={isBtnViewVisible} />
          </Col>
        </Row>
        <StatisticBar />
        <Row style={{ marginTop: '10px' }}>
          <Col xs={24} sm={24} md={24} lg={5} xl={3} className="charges-type">
            <div className="title">Loi ph tng hp</div>
            <Checkbox.Group
              options={options}
              defaultValue={['Apple']}
              onChange={this.onSelectChargeType}
              className="charge-type-checkbox"
            />
            {/* <Checkbox onChange={onChange}>
              Bo him y t
            </Checkbox> */}
            {/* <Radio.Group>
              <Radio style={radioStyle} value={1}>
                Bo him y t
              </Radio>
              <Radio style={radioStyle} value={2}>  
                V sinh cng cng
              </Radio>
              <Radio style={radioStyle} value={3}>
                K tc x lin thng
              </Radio>
            </Radio.Group> */}
          </Col>
          <Col xs={24} sm={24} md={24} lg={19} xl={21}>
            <TableData
              reload={reload}
              // formSearch
              service={getStudents}
              // paramSearch={paramSearchClass}
              columns={columns}
              rowSelection={rowSelection}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TuitionDeptChargeStatisticPage />
  </Page>
);



